import React, { FC } from "react";

import { Icon } from "../Icon/Icon";
import { colors } from "../styleguide";
import Typography from "../Typography/Typography";

import {
	avatarVariantOptions,
	ContentWrapper,
	StyledAvatar,
} from "./Avatar.styles";
import { AvatarProps } from "./Avatar.types";

const Avatar: FC<AvatarProps> = ({
	dataQA,
	className,
	size = "Medium",
	variant = "Text",
	icon,
	text,
	imageSource,
	m,
	ml,
	mr,
	mt,
	mb,
	color,
	backgroundColor,
}) => (
	<>
		<StyledAvatar
			data-qa={dataQA}
			size={size}
			className={className}
			aria-hidden
			m={m}
			ml={ml}
			mr={mr}
			mt={mt}
			mb={mb}
			backgroundColor={backgroundColor}
		>
			<ContentWrapper>
				{variant === "Placeholder" && icon && (
					<Icon
						strokeWidth={2.5}
						name={icon}
						size={avatarVariantOptions[size].iconSize}
						color={color || `${colors.black}A8`}
					/>
				)}
				{variant === "Text" && (
					<Typography
						dataQA={`${dataQA}-text`}
						component="div"
						variant={avatarVariantOptions[size].copy}
						color={color || `${colors.black}A8`}
					>
						{text?.slice(0, 2)}
					</Typography>
				)}
				{variant === "Image" && (
					<img
						data-qa={`${dataQA}-image`}
						alt=""
						src={imageSource}
					/>
				)}
			</ContentWrapper>
		</StyledAvatar>
	</>
);

Avatar.displayName = "Avatar";
export default Avatar;
